import React, { Component } from "react";
import { connect } from "react-redux";
import {
  getNavBrandState,
  getNavLinkState,
  getLoaderState,
} from "../../store/reselect";
import slide2 from "../../assets/nurse.jpg";
import Loader from "../../components/loader";
import View from "../../presentations/contact";
import { FooterBrandBody } from "../../utils/data";
import { contactAction } from "../../store/action";

class Contact extends Component {
  state = {
    title: "Contact Us",
    src: slide2,
  };

  handleContact = async (values) => {
    await this.props.onContactDispatch(values);
  };

  render() {
    let {
      state: { title, src },
      props: { brand, navlinks, isloading },
      handleContact,
    } = this;
    return (
      <>
        {isloading ? (
          <Loader />
        ) : (
          <View
            navbrand={brand}
            imageUri={src}
            navlinks={navlinks}
            headerTitle={title}
            FooterBrandBody={FooterBrandBody}
            onSubmit={handleContact}
          />
        )}
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  brand: getNavBrandState(state),
  navlinks: getNavLinkState(state),
  isloading: getLoaderState(state),
});

const mapDispatchToProps = (dispatch) => {
  return {
    onContactDispatch: (data) => {
      dispatch(contactAction(data));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Contact);
